const actions = {
  getDiscountManagements: (page, search, filter) => ({
    type: "GET_DISCOUNT_MANAGEMENTS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createDiscountManagement: () => ({
    type: "CREATE_DISCOUNT_MANAGEMENT_REQUEST"
  }),
  getItems: (locations, search) => ({
    type: "GET_ITEMS_LOCATION_DISMAN_REQUEST",
    payload: { locations: locations, search: search }
  }),
  getEmployees: locations => ({
    type: "GET_EMPLOYEES_LOCATION_DISMAN_REQUEST",
    payload: { locations: locations }
  }),
  storeDiscountManagement: data => ({
    type: "STORE_DISCOUNT_MANAGEMENT_REQUEST",
    payload: { body: data }
  }),
  editDiscountManagement: (id_discount_management, search) => ({
    type: "EDIT_DISCOUNT_MANAGEMENT_REQUEST",
    payload: { id_discount_management: id_discount_management, search: search }
  }),
  updateDiscountManagement: (id_discount_management, data) => ({
    type: "UPDATE_DISCOUNT_MANAGEMENT_REQUEST",
    payload: {
      id_discount_management: id_discount_management,
      body: data
    }
  }),
  showDiscountManagement: id_discount_management => ({
    type: "SHOW_DISCOUNT_MANAGEMENT_REQUEST",
    payload: { id_discount_management: id_discount_management }
  }),
  confirmDiscountManagement: (id_discount_management, data) => ({
    type: "CONFIRM_DISCOUNT_MANAGEMENT_REQUEST",
    payload: { id_discount_management: id_discount_management, body: data }
  }),
  cancelDiscountManagement: (id_discount_management, data) => ({
    type: "CANCEL_DISCOUNT_MANAGEMENT_REQUEST",
    payload: { id_discount_management: id_discount_management, body: data }
  }),
  uploadDocument: (id_discount_management, data) => ({
    type: "UPLOAD_DISCOUNT_MANAGEMENT_DOCUMENT_REQUEST",
    payload: {
      id_discount_management,
      body: data
    }
  }),
  downloadDocument: id_discount_management_document => ({
    type: "DOWNLOAD_DISCOUNT_MANAGEMENT_DOCUMENT_REQUEST",
    payload: { id_discount_management_document }
  }),
  deleteDocument: (
    id_discount_management_document,
    id_discount_management
  ) => ({
    type: "DELETE_DISCOUNT_MANAGEMENT_DOCUMENT_REQUEST",
    payload: {
      id_discount_management_document: id_discount_management_document,
      id_discount_management: id_discount_management
    }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
