import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";

import notification from "../../../../components/notification";
import {
  Empty,
  Input,
  Tooltip,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/promotion/discountManagement/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

class DiscountManagement extends Component {
  state = {
    filter: {
      discount_type: null,
      discount_customer_type: null,
      discount_item_type: null,
      start_date: null,
      end_date: null,
      status: null,
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getDiscountManagements } = this.props;
    getDiscountManagements();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.discountManagement.error) {
      notification("error", this.props.discountManagement.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.discountManagement.success) {
      notification("success", this.props.discountManagement.success);
      this.props.clearSuccess();
    }
  }

  clearFilterState = () => {
    this.setState({
      filter: {
        discount_customer_type: null,
        discount_item_type: null,
        discount_type: null,
        end_date: null,
        pagination: null,
        start_date: null,
        status: null
      }
    });
  };

  clearFilterSearchState = () => {
    this.setState({
      filter: {
        discount_customer_type: null,
        discount_item_type: null,
        discount_type: null,
        end_date: null,
        pagination: null,
        start_date: null,
        status: null
      },
      search: null
    });
  };

  // Handle search
  handleSearch = value => {
    const { getDiscountManagements } = this.props;
    this.clearFilterState();
    getDiscountManagements(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getDiscountManagements } = this.props;
    this.clearFilterSearchState();
    getDiscountManagements();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD");
      const end = value[1].format("YYYY-MM-DD");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle filter
  handleFilter = () => {
    const { getDiscountManagements } = this.props;
    //const { current_page } = this.props.discountManagement;
    const { filter } = this.state;
    let string = "";

    if (filter.discount_customer_type) {
      string += "discount_customer_type=" + filter.discount_customer_type;
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.discount_type) {
      string
        ? (string += "&discount_type=" + filter.discount_type)
        : (string += "discount_type=" + filter.discount_type);
    }
    if (filter.discount_item_type) {
      string
        ? (string += "&discount_item_type=" + filter.discount_item_type)
        : (string += "discount_item_type=" + filter.discount_item_type);
    }
    if (filter.status) {
      string
        ? (string += "&status=" + filter.status)
        : (string += "status=" + filter.status);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getDiscountManagements(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getDiscountManagements } = this.props;
    this.clearFilterSearchState();
    getDiscountManagements();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateDiscountManagement(id);
    } else {
      this.props.deactivateDiscountManagement(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getDiscountManagements } = this.props;
    const { search, filter } = this.props.discountManagement;
    getDiscountManagements(page, search, filter);
  };

  listAction = {
    title: "",
    dataIndex: "id_discount_management",
    rowKey: "id_discount_management",
    width: "10%",
    align: "center",
    fixed: "right",
    render: text => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>
      </div>
    )
  };

  render() {
    // Definisi kolom2 serta field2 API yang dipakai tabel
    let columns = [
      {
        title: getLanguage({ id: "discountManagements.discountCode" }),
        dataIndex: "discount_code",
        rowKey: "discount_code",
        width: "15%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "pending";
              break;
            case 2:
              className = "confirmed";
              break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <TransNo>{text}</TransNo>
            </div>
          );
        }
      },
      {
        title: getLanguage({ id: "discountManagements.discountType" }),
        dataIndex: "discount_type",
        rowKey: "discount_type",
        width: "15%",
        render: text => {
          // let className;
          let display;
          switch (text) {
            case 1:
              // className = "event";
              display = getLanguage({ id: "event" });
              break;
            case 2:
              // className = "quantity";
              display = getLanguage({ id: "quantity" });
              break;
            case 3:
              // className = "total-amount";
              display = getLanguage({ id: "totalAmount" });
              break;
            case 4:
              // className = "multiplier";
              display = getLanguage({ id: "multiplier" });
              break;
            default:
              // className = "event";
              display = getLanguage({ id: "event" });
              break;
          }

          return (
            <div style={{ display: "flex" }}>
              {/* <StatusTag className={className}>{display}</StatusTag> */}
              {display}
            </div>
          );
        }
      },
      {
        title: getLanguage({ id: "discountManagements.discountCustomerType" }),
        dataIndex: "discount_customer_type",
        rowKey: "discount_customer_type",
        width: "15%",
        render: text => {
          // let className;
          let display;
          switch (text) {
            case 1:
              // className = "customer-type";
              display = getLanguage({ id: "sidebar.customerType" });
              break;
            case 2:
              // className = "customer-code";
              display = getLanguage({ id: "customerCode" });
              break;
            default:
              // className = "-";
              display = getLanguage({ id: "All" });
              break;
          }

          return (
            <div style={{ display: "flex" }}>
              {/* <StatusTag className={className}>{display}</StatusTag> */}
              {display}
            </div>
          );
        }
      },
      {
        title: getLanguage({ id: "discountManagements.discountItemType" }),
        dataIndex: "discount_item_type",
        rowKey: "discount_item_type",
        width: "15%",
        render: text => {
          // let className;
          let display;
          switch (text) {
            case 1:
              // className = "item-category";
              display = getLanguage({ id: "discountManagements.itemCategory" });
              break;
            case 2:
              // className = "sku";
              display = getLanguage({ id: "discountManagements.itemSKU" });
              break;
            case 3:
              // className = "plu";
              display = getLanguage({ id: "discountManagements.itemPLU" });
              break;
            default:
              // className = "-";
              display = getLanguage({ id: "All" });
              break;
          }

          return (
            <div style={{ display: "flex" }}>
              {/* <StatusTag className={className}>{display}</StatusTag> */}
              {display}
            </div>
          );
        }
      },
      {
        title: getLanguage({ id: "startDate" }),
        dataIndex: "start_date",
        rowKey: "start_date",
        width: "10%",
        render: text => <span>{moment(text).format("DD MMMM YYYY")}</span>
      },
      {
        title: getLanguage({ id: "endDate" }),
        dataIndex: "end_date",
        rowKey: "end_date",
        width: "10%",
        render: text => <span>{moment(text).format("DD MMMM YYYY")}</span>
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "pending";
              display = getLanguage({ id: "pending" });
              break;
            case 2:
              className = "confirmed";
              display = getLanguage({ id: "confirmed" });
              break;
            case 3:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "pending";
              display = getLanguage({ id: "pending" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    // Data setup
    const { discountManagements } = this.props.discountManagement;
    //console.log(this.props.discountManagement); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const { loading, loadingForeign } = this.props.discountManagement;

    //console.log(permissions);
    if (
      permissions.includes("Update Discount Management") &&
      columns.length === 8
    ) {
      columns.push(this.listAction);
    }

    Object.keys(discountManagements).map((discountManagement, index) => {
      return dataSource.push({
        ...discountManagements[discountManagement],
        key: discountManagement
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.discountManagement;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="discountManagements.pageHeader" />}
            marginBottom={true}
          >
            {permissions.includes("Create Discount Management") ? (
              <Link to={`${this.props.match.path}/add`}>
                <Button
                  loading={loadingForeign}
                  disabled={loadingForeign}
                  type="primary"
                  className="mateAddInvoiceBtn"
                >
                  <IntlMessages id="discountManagements.button.add" />
                </Button>
              </Link>
            ) : null}
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={getLanguage({
                id: "discountManagements.discountCode"
              })}
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                // width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="discountManagements.discountType" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "discount_type"
                      )}
                      value={
                        this.state.filter.discount_type
                          ? this.state.filter.discount_type
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="discountManagements.filter.select.discountType" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      <Option value={1}>{getLanguage({ id: "event" })}</Option>
                      <Option value={2}>
                        {getLanguage({ id: "quantity" })}
                      </Option>
                      <Option value={3}>
                        {getLanguage({ id: "totalAmount" })}
                      </Option>
                      <Option value={4}>
                        {getLanguage({ id: "multiplier" })}
                      </Option>
                    </Select>
                  </Col>
                  <Col span={6}>
                    <label>
                      <IntlMessages id="discountManagements.discountCustomerType" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "discount_customer_type"
                      )}
                      value={
                        this.state.filter.discount_customer_type
                          ? this.state.filter.discount_customer_type
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="discountManagements.filter.select.discountCustomerType" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      <Option value={1}>
                        {getLanguage({ id: "sidebar.customerType" })}
                      </Option>
                      <Option value={2}>
                        {getLanguage({ id: "customerCode" })}
                      </Option>
                    </Select>
                  </Col>
                  <Col span={6}>
                    <label>
                      <IntlMessages id="discountManagements.discountItemType" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "discount_item_type"
                      )}
                      value={
                        this.state.filter.discount_item_type
                          ? this.state.filter.discount_item_type
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="discountManagements.filter.select.discountItemType" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      <Option value={1}>
                        {getLanguage({
                          id: "discountManagements.itemCategory"
                        })}
                      </Option>
                      <Option value={2}>
                        {getLanguage({ id: "discountManagements.itemSKU" })}
                      </Option>
                      <Option value={3}>
                        {getLanguage({ id: "discountManagements.itemPLU" })}
                      </Option>
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="discountManagements.transactionDate" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY"
                      allowClear={true}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>

                  <Col span={12}>
                    <Row justify="space-between" gutter={1} type="flex">
                      <Col span={8}>
                        <label>Status</label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "status"
                          )}
                          value={
                            this.state.filter.status
                              ? this.state.filter.status
                              : []
                          }
                          style={{ width: "95%" }}
                          placeholder="Status"
                          optionFilterProp="children"
                          filterOption={true}
                          allowClear={true}
                        >
                          <Option value={1}>
                            {getLanguage({ id: "pending" })}
                          </Option>
                          <Option value={2}>
                            {getLanguage({ id: "confirmed" })}
                          </Option>
                          <Option value={3}>
                            {getLanguage({ id: "cancelled" })}
                          </Option>
                        </Select>
                      </Col>
                      <Col span={8}>
                        <label>
                          <IntlMessages id="dataPerPage" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "pagination"
                          )}
                          value={
                            this.state.filter.pagination
                              ? this.state.filter.pagination
                              : page_size
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value={10}>
                            10
                          </Option>
                          <Option key={2} value={25}>
                            25
                          </Option>
                          <Option key={3} value={50}>
                            50
                          </Option>
                          <Option key={4} value={100}>
                            100
                          </Option>
                        </Select>
                      </Col>
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.submit" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                      <Col span={8} />
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {discountManagements.length === 0 &&
            !this.props.discountManagement.loading ? (
              <Empty
                description={
                  <IntlMessages id="discountManagements.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    discountManagement: state.discountManagement,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(DiscountManagement);
